<template>
  <view class="homePage">
    <!--未添加过设备-->
    <view class="deviceNone" v-if="deviceList.length === 0">
      <view class="deviceIcon">
        <image class="deviceImg" src="/static/index/noConnect.png" mode="widthFix"></image>
        <view class="deviceText">请添加设备</view>
      </view>
    </view>
    <!--添加过设备-->
    <view class="deviceListWrap" v-if="deviceList.length !== 0">
      <view class="deviceItemWrap" v-for="(item, index) in deviceList" :key="index" @click="toSkipLivePlay(item)">
        <view class="deviceLeft">
          <image class="icon" src="../../static/index/deviceIcon.png" mode="widthFix"></image>
        </view>
        <view class="deviceRight">
          <view class="deleteIcon">
            <image src="../../static/index/delete.png" mode="widthFix" @click.stop="deleteDevice(item, index)"></image>
          </view>
          <view class="deviceName">
            <view class="deviceNameTitle">{{item.deviceName}}</view>
            <view class="deviceStatus">
              <view :class="['cirle', item.status ? 'isOnline' : 'noOnline']"></view>
              <text class="statusTitle">{{item.status ? '在线' : '离线'}}</text>
            </view>
          </view>
        </view>
      </view>
    </view>
    <button class="addDeviceBtn" type="primary" @click="addDeviceBtn">添加设备</button>
    <!-- 测试接口 -->
    <!-- <view>{{deviceWifiName}}--{{appWifiName}}</view> -->
    <!-- <view>{{data}}</view> -->
    <!-- <button class="addDeviceBtn" type="primary" @click="getDataText">获取数据</button> -->
    <!--删除提示信息-->
    <uni-popup ref="popup" type="center">
      <uni-popup-dialog type="warn" title="删除" mode="base" :content="content" :before-close="true" @close="closePopup"
        @confirm="confirmPopup"></uni-popup-dialog>
    </uni-popup>
  </view>
</template>

<script>
  var sand = uni.requireNativePlugin("sand-plugin-wifi")
  var page
  export default {
    data() {
      return {
        deviceList: [],
        idx: null, // 要删除设备索引,
        content: "", // 要删除设备名称
        tokenInfo: null,
        deviceWifiName: '',
        appWifiName: "",
        wifiInfo: "",
        data: ""
      }
    },
    onLoad() {
      // 获取设备信息
      // this.deviceList = JSON.parse(uni.getStorageSync("deviceInfo"))
      // this.deviceInfo = uni.getStorageSync("wifiInfo")
      page = this
      // 获取登录信息
      this.getLoginInfo()
      // 获取连接 wifi 名字 (通过手机设备获取连接的 wifi 名称)
      this.getWifiNameBySetting()

    },
    onShow() {
      uni.showTabBar()
      this.getLoginInfo()
    },
    methods: {
      // 测试事件
      getDataText() {
        let params = {
          "method": "configManager.getConfig",
          "params": {
            "name": "General"
          },
          "id": 1080,
          "session": 123
        }
        this.$axios(params).then((res) => {
          this.data = JSON.stringify(res.data.params.table)
          console.log(8585, data)
        })
      },
      // 获取登录信息
      getLoginInfo() {
        let params = {
          "method": "login.negotiate",
          "params": {
            "userName": "admin"
          },
          "session": 1234,
          "id": 123456
        }
        this.$axios(params).then((res) => {
          this.tokenInfo = res.data.params
          this.toLogin(this.tokenInfo)
        })

      },
      toLogin(tokenInfo) {
        // 密码加密
        const {
          authorization,
          random
        } = tokenInfo
        let params = {
          authorization: authorization,
          password: `${random}admin123`
        }
        let encryptedPw = this.$common.generateKey(params) // 加密后的密码
        let param = {
          "method": "login",
          "params": {
            "userName": "admin",
            "password": encryptedPw
          },
          "session": 1234,
          "id": 123456
        }

        this.$axios(param).then((res) => {
          const token = res.header['refresh-token']
          uni.setStorageSync('token', token)
          this.getDataDevice()
        })

      },
      // 登录信息结束
      // 获取设备 wifi 名称
      getDataDevice() {
        const param = {
          "method": "RemoteWLanManager.getHostApdConfig",
          "params": null,
          "id": 1284,
          "session": "de804258edea1171ea971f3c32aeef39"
        }
        this.$axios(param).then((res) => {
          this.deviceWifiName = res.data.params.SSID
          console.log(1212, this.deviceWifiName === this.appWifiName)
          if (this.appWifiName === res.data.params.SSID) { // this.appWifiName === res.data.params.SSID 判断设备名称是否与连接的手机 wifi 名称是否一致
            this.getDeviceInfo()
          }
        })
      },
      // 获取手机连接的 wifi 名称 
      getWifiNameBySetting() {
        console.log(2, sand)
        this.appWifiName = "rtwap"
        this.wifiInfo = this.$common.getAppWifiInfo()
        console.log(132132, this.wifiInfo)
      },
      // 获取设备名称
      getDeviceInfo() {
        const params = {
          method: "magicBox.getDeviceType",
          params: {},
          id: 1051,
          session: 123456
        }
        this.$axios(params).then((res) => {
          console.log(141414, res.data.params.type)
          this.getDeviceInfoArr(res.data.params.type)
        })
      },
      // 获取设备信息数组
      getDeviceInfoArr(name) {
        const deviceInfo = JSON.parse(uni.getStorageSync('deviceInfo'))
        if (deviceInfo.length === 0) {
          deviceInfo.push({
            deviceName: name,
            status: true
          })

        } else {
          const isfind = deviceInfo.find((item) => {
            return item.deviceName === name
          })
          if (isfind) {
            deviceInfo.forEach((item) => {
              if (item.deviceName === name) {
                item.status = true
              } else {
                item.status = false
              }
            })
          } else {
            deviceInfo.forEach((item) => {
              item.status = false
            })
            deviceInfo.push({
              deviceName: name,
              status: true
            })
          }
        }
        this.deviceList = [...deviceInfo] // 设备数据
        uni.setStorageSync("deviceInfo", JSON.stringify(deviceInfo))
      },


      // 删除设备
      deleteDevice(item, index) {
        this.idx = index
        this.content = `是否要删除该${item.deviceName}设备?`
        this.$refs.popup.open()
      },
      closePopup() {
        this.$refs.popup.close()
      },
      // 删除弹窗事件
      confirmPopup() {
        this.deviceList.splice(this.idx, 1)
        uni.setStorageSync("deviceInfo", JSON.stringify(this.deviceList))
        this.$refs.popup.close()
      },
      // 跳转直播
      toSkipLivePlay(item, index) {
        if (item.status) { // item.status 状态判断跳转
          this.$uniapi.goGavigate(`../livePlay/livePlay?deviceName=${item.deviceName}`)
        } else { // 直接连接 wifi (搜索设备, 并连接设备)

        }
      },
      // 添加设备
      addDeviceBtn() {
        this.$common.openAppWifi()
      }
    }
  }
</script>

